/* TabNavigation.module.css */

.section {
  background-color: white;
  border-bottom: 1px solid #e5e7eb;
}

.container {
  max-width: 1152px;
  margin: 0 auto;
  padding: 0 16px;
}

@media (min-width: 640px) {
  .container {
    padding: 0 24px;
  }
}

@media (min-width: 1024px) {
  .container {
    padding: 0 32px;
  }
}

.tabsList {
  display: flex;
  gap: 32px;
}

.tabButton {
  padding: 16px 8px;
  border-bottom: 2px solid transparent;
  font-weight: 500;
  font-size: 14px;
  background: none;
  border-top: none;
  border-left: none;
  border-right: none;
  cursor: pointer;
  color: #6b7280;
  transition: color 0.2s, border-color 0.2s;
}

.tabButton:hover {
  color: #374151;
}

.tabButtonActive {
  border-bottom-color: #3b82f6;
  color: #2563eb;
}

.tabButtonActive:hover {
  color: #2563eb;
}